<template>
  <section>
    <!-- -->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="search">
        <el-form-item v-for="(val, key, index) in search" :key="index">
          <el-input v-if="key!='check_status'" v-model="search[key]" :placeholder="searchName[key]"></el-input>
        </el-form-item>
        <el-form-item>
          <el-select v-model="search.check_status" placeholder="">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="searchPatient" size="small"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-refresh-right" @click="getPatients" size="small"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addPatient" size="small"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!-- -->
    <el-table :data="patientsList" highlight-current-row v-loading="listLoading"
              style="width: 100%;" height="500"><!--height-->
      <!-- <el-table-column type="selection" width="55">
      </el-table-column> -->
      <el-table-column type="index" width="40">
      </el-table-column>
      <el-table-column prop="name" label="" width="90" sortable>
      </el-table-column>
      <el-table-column prop="serial" label="" width="120" sortable>
      </el-table-column>
      <el-table-column prop="hospital" label="" width="150">
      </el-table-column>
      <el-table-column prop="address" label="" width="150">
      </el-table-column>
      <el-table-column prop="expert" label="" width="80">
      </el-table-column>
      <!-- <el-table-column prop="owner" label="" width="80">
      </el-table-column> -->
      <el-table-column prop="degree_of_completion" label="" width="90">
        <template v-slot="scope">
          <el-progress type="circle"
                      :percentage="Math.round(Number(scope.row.degree_of_completion))"
                      :width="40" :color="customColors">
          </el-progress>
        </template>
      </el-table-column>
      <el-table-column prop="check_status" label="" width="110">
        <template v-slot="scope">
          <el-tag v-if="scope.row.check_status==''" type="warn">{{scope.row.check_status}}</el-tag>
          <el-tag v-if="scope.row.check_status==''" type="success">{{scope.row.check_status}}</el-tag>
          <el-tag v-if="scope.row.check_status==''" type="danger">{{scope.row.check_status}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" width="640">
        <template v-slot="scope">
          <el-button-group>
          <el-button type="btn-info" size="small" @click="openDataForm(scope.$index, scope.row, 'info')"></el-button>
          <el-button type="btn-summary" size="small" @click="openDataForm(scope.$index, scope.row, 'summary')"></el-button>
          <el-button type="btn-history"    size="small" @click="openDataForm(scope.$index, scope.row, 'history')"></el-button>
          <el-button type="btn-experiment" size="small" @click="openDataForm(scope.$index, scope.row, 'experiment')"></el-button>
          <el-button type="btn-bxray" size="small" @click="openDataForm(scope.$index, scope.row, 'bxray')">B</el-button>
          <el-button type="btn-clinical" size="small" @click="openDataForm(scope.$index, scope.row, 'clinical')"></el-button>
          <el-button type="btn-cure" size="small" @click="openDataForm(scope.$index, scope.row, 'cure')"></el-button>
          </el-button-group>
          <el-button type="danger" size="mini" style="margin-left:8px" v-if="is_admin"
                    @click="checkPatient(scope.$index, scope.row)" icon="el-icon-view" circle>
                    </el-button>
          <el-button v-show="scope.row.check_status!=''" icon="el-icon-delete" circle
                      type="danger" size="mini" style="margin-left:8px" 
                      @click="delPatient(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
      <!-- v-on,,$event, -->
      <!-- v-onFunction | Inline Statement,() FunctionInline Statement-->
      <el-pagination layout="total, prev, pager, next, jumper"
                    @current-change="pagination_flag ? handleListPagination($event,page) : handleSearchPagination($event,search_page)"
                    :page-size="10" :total="totalNum"
                    style="float:right;">
      </el-pagination>
    </el-col>

    <!-- dialog -->
    <InfoForm ref="info"></InfoForm>
    <!-- dialog -->
    <SummaryForm ref="summary"></SummaryForm>
    <!-- dialog -->
    <HistoryForm ref="history"></HistoryForm>
    <!-- dialog -->
    <ExperimentForm ref="experiment"></ExperimentForm>
    <!-- Bdialog -->
    <BxrayForm ref="bxray"></BxrayForm>
    <!-- dialog -->
    <ClinicalForm ref="clinical"></ClinicalForm>
    <!-- dialog -->
    <CureForm ref="cure"></CureForm>

    <!-- dialog -->
    <AddPatient ref="addPatient" ></AddPatient>
    <!-- dialog -->
    <CheckPatient ref="checkPatient" ></CheckPatient>

  </section>
</template>

<script>
import util from '@/common/js/util'
// axios,express
import {apiGetPatientsList, apiRemovePatient, apiSearchPatient, apiGetPatientDataForm} from '@/api/api-prj002'
// 
import {AddPatient, CheckPatient} from '@/components/prj002/forms'
import {InfoForm,SummaryForm,HistoryForm,ExperimentForm,BxrayForm,ClinicalForm,CureForm} from '@/components/prj002/forms'
export default {
  name:'Table',
  components:{AddPatient,CheckPatient,InfoForm,SummaryForm,HistoryForm,ExperimentForm,BxrayForm,ClinicalForm,CureForm},
  data () {
    return {
      is_admin:true,
      search: {
        name: '', phone:'', hospital:'', address:'', check_status:''//career:'',birth:''
      },
      searchName: {name:'',phone:'',hospital:'',address:''},//career:'',birth:'',
      patientsList: [], // 
      totalNum: 0, // 
      page: 1, //
      search_page: 1, //
      listLoading: false,
      pagination_flag: true, //true,false
      customColors: [
          {color: '#f56c6c', percentage: 20},
          {color: '#e6a23c', percentage: 50},
          {color: '#1989fa', percentage: 80},
          {color: '#5cb87a', percentage: 100}
        ]
    }
  },
  methods: {
    // dialog
    addPatient () {
      this.$refs.addPatient.$emit("addEvent")
    },
    // 
    checkPatient (index, row) {
      const checkData = {
        check:row.check,// checkurl
        check_status:row.check_status,
        reason_for_check:row.reason_for_check
        }
      this.$refs.checkPatient.$emit("checkEvent",checkData)
    },
    // 
    delPatient: function (index, row) {
      this.$confirm(', ', '', {type: 'warning',center: true})
      .then(() => {
        this.listLoading = true
        let para = { url: row.url }
        apiRemovePatient(para).then((res) => {
          this.listLoading = false
          if (res.data.msg) {
            this.$message({message: '', type: 'error'})
          } else {
            this.$message({message: '', type: 'success'})
          }
          this.getPatients()
        })
      }).catch()
    },
    // 
    searchPatient () {
      console.log('',this.search)
      let para = {
        page: this.search_page,
        search: this.search
      }
      this.pagination_flag = false
      apiSearchPatient(para).then( (res) => {
        this.patientsList = res.data.searchResults
        this.totalNum = res.data.searchResultsNum
        this.listLoading = false
      })
    },
    handleListPagination (currentPage) {
      console.log('',currentPage)
      this.page = currentPage,
      this.getPatients()
    },
    handleSearchPagination (currentPage) {
      console.log('',currentPage)
      this.search_page = currentPage
      this.searchPatient()
    },
    // 
    getPatients () {
      this.search = {name: '', phone:'', hospital:'', address:'', check_status:''}//career:'', birth:''
      let para = {page: this.page}
      this.listLoading = true
      apiGetPatientsList(para).then((res) => {
        console.log(res.data)
        this.patientsList = res.data.patientsList
        this.is_admin = res.data.is_admin
        this.totalNum = res.data.totalNum
        this.listLoading = false
      })
    },

    //Form
    openDataForm (index, row, formName) {
      console.log('formName',formName)
      // DataForm,,
      if (row[formName] == null) {
        console.log('',formName)
        // DataFormurl,urlinfourl
        this.$refs[formName].$emit("openEvent", {exist:false, formData:{info:row.info}, formName:formName } )
      } else {
      // DataForm,,
        console.log('',formName)
        // DataForm
        console.log('row[formName]',row[formName])
        let para = {page: this.page, url: row[formName]}
        apiGetPatientDataForm(para)
        .then((res)=> {
          console.log('DataForm',res.data)
          this.$refs[formName].$emit("openEvent", {exist:true, formData:res.data, formName:formName, check_status:row.check_status})
        })
        .catch()
      }
    },

  },
  mounted () {
    this.getPatients()
  }
}

    // selsChange: function (sels) {
    //   this.sels = sels
    // },
    // // 
    // batchRemove: function () {
    //   var ids = this.sels.map(item => item.id).toString()
    //   this.$confirm('', '', {
    //     type: 'warning'
    //   }).then(() => {
    //     this.listLoading = true
    //     let para = { ids: ids }
    //     batchRemoveUser(para).then((res) => {
    //       this.listLoading = false
    //       this.$message({
    //         message: '',
    //         type: 'success'
    //       })
    //       this.getPatients()
    //     })
    //   }).catch(() => {
    //   })
    // }
</script>

<style scoped>
.el-button--btn-info {
  background:#a8d8ea;
  border:1px solid #a8d8ea
}
.el-button--btn-summary {
  background:#aa96da;
  border:1px solid #aa96da
}
.el-button--btn-history {
  background:#fcbad3;
  border:1px solid #fcbad3
}
.el-button--btn-experiment {
  background:#ffffd2;
  border:1px solid #ffffd2
}
.el-button--btn-bxray {
  background:#c7f0db;
  border:1px solid #c7f0db
}
.el-button--btn-clinical {
  background:#41b6e6;
  border:1px solid #41b6e6
}
.el-button--btn-cure {
  background:#d7d1c9;
  border:1px solid #d7d1c9
}
.el-button--myinfo {
background:red;
border:1px solid red;
}
</style>



